import { cssProps } from '@oracle/oraclejet-cssprops';
import { ItemMetadata } from '../utils/UNSAFE_dataProvider';
import { ComponentChildren, Fragment } from 'preact';
import { MessageVariant } from './Message.types';
import { isValidValueForProp } from './MessageFormattingUtils';

const messageDetailStyles = {
  // TODO: Reevaluate once the TEXT component is available (JET-46891)
  base: cssProps`${{
    marginTop: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-spacing-2x)',
    overflowWrap: 'anywhere'
  }}`,

  banner: cssProps`${{
    fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-sm-font-size)',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-text-color-primary)'
  }}`,
  inline: undefined
};

type MessageWithDetailText = {
  /**
   * The detail text for the message
   */
  detail?: string;
};

/**
 * Structure of template item used for dynamic templates
 */
type MessageTemplateItem<K, D> = {
  /**
   * The data for the current message
   */
  data: D;

  /**
   * The zero-based index of the current message
   */
  index: number;

  /**
   * The key for the current message
   */
  key: K;

  /**
   * The metadata for the current message
   */
  metadata?: ItemMetadata<K>;
};

/**
 * Props for the Detail component
 */
type Props<K, D> = {
  /**
   * The template item for rendering the detail content
   */
  item: MessageTemplateItem<K, D>;

  /**
   * A custom renderer for rendering the detail content
   */
  renderer?: (item: MessageTemplateItem<K, D>) => ComponentChildren;

  /**
   * Style variant
   */
  variant?: MessageVariant;
};

/**
 * Default renderer for rendering the detail content.
 *
 * @param item The template item object
 * @returns Rendered detail content
 */
function defaultDetailRenderer<D extends MessageWithDetailText>(
  item: MessageTemplateItem<unknown, D>
): ComponentChildren {
  const { detail } = item.data;
  // If the detail is null or an empty string, do not render the
  // content row
  if (!isValidValueForProp(detail)) {
    return null;
  }
  return <Fragment>{detail}</Fragment>;
}

/**
 * Detail Component for rendering the detail content of the Message
 */
function MessageDetail<K, D>({
  item,
  renderer = defaultDetailRenderer,
  variant = 'banner'
}: Props<K, D>) {
  const renderedContent = renderer(item);

  if (renderedContent == null) return null;

  // If detail content is rendered, then wrap it in a div with specified style classes
  const classes = `oj-c-message${variant}-detail ${messageDetailStyles.base} ${messageDetailStyles[variant]}`;
  return <div class={classes}>{renderedContent}</div>;
}

export { MessageDetail, MessageTemplateItem };
